.button__container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.button {
    cursor: pointer;
    background-color: blue;
    color: white;
    border: 1px solid blue;
    border-radius: 1rem;
    padding: 1rem 2rem;
    font-size: 1.2rem;
    font-weight: 600;
    transition: all 0.3s ease;
}

.button:hover {
    background-color: white;
    color: blue;
}
